<template>
    <div class="container-fluid m-3">
    <h1 class="text-danger text-center mb-4">Ming 的小功課</h1>
        <div class="mb-2">
            <button type="button" class="btn btn-primary btn-lg me-3" @click="showTopic('ming')">Ming的題目</button>
            <!-- <button type="button" class="btn btn-primary btn-lg me-3" @click="showTopic('round')">亂數生成題目</button> -->
            <button type="button" class="btn btn-success btn-lg" @click="calculateAnswer">產生答案</button>
        </div>
        <div class="row p-2" :style="`height:${scrollHeight}px`">
            <div class="col me-4 p-3 border">
                <h4 class="">題目：</h4>
                {{topic}}
                
            </div>
            <div class="col p-3 me-5 border">
                <h4 >答案：</h4>
                <h5 class="fw-bold">組合件的總價值 = {{totalPrice}}</h5>
                <h5 class="fw-bold">各個零件的使用數量 : </h5>
                <ul v-for="(data, key) in recordSort" :key="key">
                    <li>{{data}} : {{record[data].quantity}}</li>
                </ul>
            </div>
        </div>
    </div>
    <!-- 亂數生成題目 modal -->
  <div
    class="modal fade"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    tabindex="-1"
    aria-labelledby="staticBackdropLabel"
    aria-hidden="true"
    ref="roundTopicModal"
  >
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title tw-border-start fw-bolder">亂數生成題目</h5>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
            <div class="mb-3 d-flex">
                <label for="nodeDeep" class=" fs-4">節點最大深度：</label>
                <div>
                    <input type="number" class="form-control" v-model="randomSetting.maxDeep" placeholder="請輸入節點最大深度" id="nodeDeep">
                </div>
            </div>
            <div class="mb-3 d-flex">
                <label for="nodeWidth" class=" fs-4">節點最大寬度：</label>
                <div>
                    <input type="number" class="form-control" v-model="randomSetting.maxWidth" placeholder="請輸入節點最大寬度" id="nodeWidth">
                </div>
            </div>
            <div class="mb-3 d-flex">
                <label for="price" class=" fs-4">價格區間：</label>
                <div>
                    <input type="number" class="form-control" v-model="randomSetting.maxPrice" placeholder="請輸入最大價格" id="price">
                </div>
            </div>
            <div class="mb-3 d-flex">
                <label for="quantity" class=" fs-4">數量區間：</label>
                <div>
                    <input type="number" class="form-control" v-model="randomSetting.maxQuantity" placeholder="請輸入最大數量" id="quantity">
                </div>
            </div>
        </div>
        <div class="modal-footer border-0">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            關閉
          </button>
          <button type="button" class="btn btn-success" @click="randomCreateTopic">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
                // modal
                roundTopicModal: {},
                // data
                scrollHeight: window.innerHeight - 210,
                randomSetting: {
                    maxDeep: 5,
                    maxWidth: 5,
                    maxPrice: 100,
                    maxQuantity: 5,
                },
                layerWidth: [],
                topic: null,
                totalPrice: 0,
                record: {},
                recordSort: [],
            }
        },
        mounted() {
            this.createModals(['roundTopicModal'])
        },
        methods: {
            showTopic(status) {

                if (status === 'ming') {
                    this.createMingTopic()
                } else if (status === 'round') {
                    this.roundTopic = '亂數生成題目'
                    this.roundTopicModal.show()
                }
                
            },
            // 生成 ming 題目
            createMingTopic() {
                const p1 = {name:'p1', price:10}
                const p2 = {name:'p2', price:20}
                const p3 = {name:'p3', price:30}
                const p4 = {name:'p4', price:40}
                const p5 = {name:'p5', price:50}
                let a1 = {
                    name:'a1',
                    links: [{quantity:1, uses: p1},{quantity:2, uses: p2}]
                }
                let a2 = {
                    name:'a2',
                    links: [{quantity:3, uses: a1},{quantity:4, uses: p3}]
                }
                let a3 = {
                    name:'a3',
                    links: [{quantity:5, uses: a1},{quantity:6, uses: a2},{quantity:7, uses: p4}]
                }
                let a4 = {
                    name:'a4',
                    links: [{quantity:8, uses: a3},{quantity:9, uses: p5},{quantity:10, uses: p1}]
                }
                console.log(a4)
                this.topic = a4
            },
            // 亂數生成題目
            randomCreateTopic() {
                for (let i = 0; i < this.randomSetting.maxDeep; i++ ) {
                    const linkLength = Math.floor(Math.random() * this.randomSetting.maxWidth ) + 1
                }
            },
            // 產生答案
            calculateAnswer() {
                if (!this.topic) return alert('請先選擇題目')
                this.totalPrice = 0
                this.record = {}
                this.record[this.topic.name] = {
                    quantity: 1
                }
                this.totalPrice = this.recursive(this.topic)
                this.recordSort = Object.keys(this.record).sort((a,b)=> {
                    if(a<b) return -1
                    else return 0
                })
            },
            // 計算子層 (遞迴)
            recursive(sublayer) {
                let subPrice = 0
                sublayer['links'].forEach(item => {
                    this.calculateQuantity(item.uses.name, item.quantity)
                    if (item.uses['links'] === undefined) {
                        subPrice += item.quantity * item.uses.price
                    } else {
                        let test = this.recursive(item.uses)
                        subPrice += test * item.quantity
                    }
                })
                return subPrice
            },
            // 計算每個零件個數
            calculateQuantity(name, quantity) {
                if (this.record[name] === undefined) {
                    this.record[name] = {
                        quantity: 0
                    }
                }
                this.record[name] = {
                    quantity: this.record[name].quantity + quantity
                }
            }

        }
    }
</script>
